#top-section {
  background: url(../../images/backgrounds/sf-downtown-night2.jpg);
  height: 100%;
  width: 100%;
  background-size: 100%;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  overflow: hidden;
  margin: 0;
  position: relative;
  background-attachment: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  color: white;
  text-shadow:
 -1px -1px 0 #000,
  1px -1px 0 #000,
  -1px 1px 0 #000,
   1px 1px 0 #000;
}

#top-section h1, #top-section h3{
  margin-left: 10px;
  margin-right: 10px;
}

#top-section #down-arrow{
  width: 50px;
  position: absolute;
  bottom: 50px;
  opacity: 0.5;
  cursor: pointer;
  -webkit-transition: 0.5s all ease-out;
  -o-transition: 0.5s all ease-out;
  transition: 0.5s all ease-out;
}

#top-section #down-arrow:hover{
  opacity: 1;
}



