#projects-section h2{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 2rem;
  color: #3e3e3e;
  margin: 0rem auto 45px;
  padding-top: 1.5rem;
}

@media print, screen and (min-width: 45em) {
  #skills-section h2{
    font-size: 2.5rem;
  }
}

#projects-section .masonry-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
}

#projects-section .project-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 350px;
  width: 350px;
  background: transparent;
  border: 1px solid #5d5a5a;
  border-radius: 5px;
  margin-bottom: 50px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  color: white;
  background: black;
}

#projects-section .project-card h4 {
  display: -ms-flexbox;
  display: flex;
  margin: 15px 0px 25px 0px;
}

#projects-section .project-card h6 {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  bottom: 25px;
}

#projects-section .project-card img {
  border-top: 1px solid white;
  border-bottom: 1px solid white;
}

.modal-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.modal-body-top {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.modal-content .title {
  margin-bottom: 25px;
}

.modal-content .video-container {
  width: 80%;
  padding-right: 25px;
}

.modal-content .modal-links a h4 {
  margin-bottom: 50px;
}


.modal-content .video-react-control-bar {
  display: none !important;
}

.modal-content .video-react, .modal-content img {
  outline: none;
  pointer-events: none;
  margin-bottom: 25px;
}

@media print, screen and (min-width: 45em) {
  #projects-section .project-card {
    margin: 0px 100px 100px 100px;
  }
}



